import { Controller, Block } from '@ali/4ever-cangjie';
import Table from '../../mo/models';
import getDataTableSelectionByTable from '../utils/getDataTableSelectionByTable';

export default function isSelectWholeTable(controller: Controller, { node }: { node: Block }) {
  const tableSelection = Table.isTable(node) && getDataTableSelectionByTable(controller, node);
  return (
    tableSelection
    && tableSelection.startRowIndex === 0
    && tableSelection.startColIndex === 0
    && tableSelection.endRowIndex >= node?.nodes?.length - 1
    && tableSelection.endColIndex >= (node?.data?.colsWidth || []).length - 1
  );
}
